import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cityDetails: {},
    weatherDetails: [],
    gotTheDetails: false,
    currentDay: 0
  },
  getters: {
    getCityDetails: state => {
      return state.cityDetails
    },
    getWeatherDetails: state => {
      return state.weatherDetails[state.currentDay]
    },
    getGotTheDetails: state => {
      return state.gotTheDetails
    }
  },
  mutations: {
    receiveCityDetails: (state, payload) => {
      state.cityDetails = payload
    },
    receiveWeatherDetails: (state, payload) => {
      state.weatherDetails = payload
    },
    changeGotTheDetails: (state, payload) => {
      state.gotTheDetails = payload
    },
    updateCurrentDay: (state, payload) => {
      state.currentDay = payload
    }
  },
  actions: {
    receiveCityDetails: (context, payload) => {
      console.log(payload)
      axios.get(`http://dataservice.accuweather.com/locations/v1/cities/search?apikey=D3P03enScVUGcBN5An01q9M7coRiAV2q&q=${payload}`)
        .then(response => {
          context.commit('receiveCityDetails', response.data[0])
          context.dispatch('receiveWeatherDetails', response.data[0].Key)
        })
    },
    receiveWeatherDetails: (context, payload) => {
      axios.get(`http://dataservice.accuweather.com/forecasts/v1/daily/5day/${payload}?apikey=D3P03enScVUGcBN5An01q9M7coRiAV2q`)
        .then(response => {
          context.commit('receiveWeatherDetails', response.data.DailyForecasts)
          context.commit('changeGotTheDetails', true)
        })
    },
    updateCurrentDay: (context, payload) => {
      context.commit('updateCurrentDay', payload)
    }

  }
})
