<template>
  <div id="app" @click="global($event)">
    <h1 class="app-title">Weather App</h1>
    <form class="app-form" @submit.prevent="submitForm(inputCity)">
      <label for="input-city">
        <h2>Search for the city</h2>
      </label>
      <br />
      <input
        type="text"
        id="input-city"
        class="input-city"
        v-model="inputCity"
        placeholder="City name..."
      />
      <button type="submit" class="submit-btn">Submit</button>
    </form>
    <div class="app-details-wrapper" v-if="getGotTheDetails">
      <h1 class="city-details-title">City Details:</h1>
      <div class="app-city-details">
        <h3>
          City name:
          <span>{{getCityDetails.EnglishName}}</span>
        </h3>
        <h3>
          Country name:
          <span>{{getCityDetails.Country.EnglishName}}</span>
        </h3>
        <h3>
          Time Zone:
          <span>{{getCityDetails.TimeZone.Code}}</span>
        </h3>
      </div>
      <h1 class="weather-details-title">Weather Details:</h1>
      <div class="app-weather-details">
        <button class="btn-1">
          <i class="fas fa-caret-left"></i>
        </button>
        <div class="day">{{currentDayString}}</div>
        <div class="app-weather-day">
          <h3>
            Temperature (MIN/MAX):
            <span>{{Math.round((5/9) * (getWeatherDetails.Temperature.Minimum.Value-32))}}</span>&deg;C/
            <span>{{Math.round((5/9) * (getWeatherDetails.Temperature.Maximum.Value-32))}}</span>&deg;C;
          </h3>
          <h3>
            Weather Condition (DAY):
            <span>{{getWeatherDetails.Day.IconPhrase}}</span>
          </h3>
          <h3>
            Weather Condition (NIGHT):
            <span>{{getWeatherDetails.Night.IconPhrase}}</span>
          </h3>
        </div>
        <button class="btn-2">
          <i class="fas fa-caret-right"></i>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "app",
  data() {
    return {
      inputCity: "",
      previousCity: "",
      currentDay: 0,
      currentDayString: "Today"
    };
  },
  computed: {
    ...mapGetters(["getCityDetails", "getWeatherDetails", "getGotTheDetails"])
  },
  methods: {
    submitForm(inputCity) {
      if(this.inputCity !== this.previousCity) {
        this.$store.dispatch("receiveCityDetails", this.inputCity);
      }
      this.previousCity = this.inputCity
      this.inputCity = "";
    },
    oneUp() {
      this.currentDay++;
      if (this.currentDay === 5) {
        this.$store.dispatch("updateCurrentDay", 0);
        this.currentDay = 0;
        return;
      }
      this.$store.dispatch("updateCurrentDay", this.currentDay);
    },
    oneDown() {
      this.currentDay--;
      if (this.currentDay === -1) {
        this.$store.dispatch("updateCurrentDay", 4);
        this.currentDay = 4;
        return;
      }
      this.$store.dispatch("updateCurrentDay", this.currentDay);
    },
    global(e) {
      if (
        e.target.className.includes("btn-1") ||
        e.target.className.includes("fa-caret-left")
      ) {
        this.oneDown();
      }
      if (
        e.target.className.includes("btn-2") ||
        e.target.className.includes("fa-caret-right")
      ) {
        this.oneUp();
      }
    }
  },
  watch: {
    currentDay() {
      switch (this.currentDay) {
        case 0:
          this.currentDayString = "Today";
          break;
        case 1:
          this.currentDayString = "Tomorrow";
          break;
        case 2:
          this.currentDayString = "In two days";
          break;
        case 3:
          this.currentDayString = "In three days";
          break;
        default:
          this.currentDayString = "In four days";
      }
    }
  }
};
</script>

